body{
  background-color: #e7e6e6;
}

.app{
  background:linear-gradient(-45deg, #57cfb0, #2ab5d3);
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
}

#to-do-form input{
  background: rgb(27, 112, 137);
  border:0px;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
font-size: 18px;
color:#fff;
}

#to-do-form input::placeholder{
  color: rgba(255,255,255,0.5);

}

#to-do-form input:focus{
  outline: none;
}

#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  border:0;
  background-color: rgb(248, 147, 32);
  cursor: pointer;
  font-weight: bolder;
  color:#fff;
}

#to-do-form button:focus{
  outline:none;
}